@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Orbitron', sans-serif}body{background:#7895c2}#calculator-main{background:#3d354d;display:grid;grid-template-columns:auto auto auto auto;width:400px;margin:70px auto 0 auto;padding:5px;border-style:outset}#calculator-main #history{grid-column:1/5;text-align:right;color:#ded202;font-size:20px;padding-right:5px;word-wrap:anywhere}#calculator-main #display{text-align:right;grid-column:1/5;color:#d6e2ff;font-size:25px;padding-right:5px;word-wrap:anywhere}#calculator-main button{border:1px solid #3d354d;color:#d6e2ff;background:#43474d;text-align:center;text-decoration:none;font-size:20px;cursor:pointer;padding:25px 0;-webkit-transition-timing-function:ease;transition-timing-function:ease}#calculator-main button::-moz-focus-inner{border:0}#calculator-main button:focus{outline:none}#calculator-main button:hover{color:#fcaba9;border:1px solid #fcaba9}#calculator-main #clear{background:#870808;grid-column:1/3}#calculator-main #equals{grid-row:6/8;grid-column:4/5;background:#4e3bff}#calculator-main #zero{grid-row:7/8;grid-column:1/3}#calculator-main #divide,#calculator-main #multiply,#calculator-main #subtract,#calculator-main #add{background:#7d7f82}@media screen and (max-width: 420px){#calculator-main{width:100vw;height:100vh;margin:0;padding:0;border-style:none;max-height:500px}}

